a:link {
    text-decoration: none;
}
  
ul li {
    color: white;
}

.nav-bar{
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    background: transparent;
    width: 100%;
    position: fixed;

    transition-timing-function: ease-in;
    transition: 0.5s;
}

.nav-bar.active {
    min-height: 6vh;
    background-color: #36454f;
}
  
.nav-links{
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}